html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: initial;
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  color: inherit;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.section-title {
  font-size: 32px;
  line-height: 36px;
  color: var(--color-accent);

  @include tablet {
    font-size: 27px;
    line-height: 30px;
  }
  @include tablet {
    font-size: 25px;
    line-height: 28px;
  }
}

.descr {
  font-size: 16px;
  line-height: 24px;
  color: var(--color-accent);

  @include mobile {
    font-size: 14px;
    line-height: 18px;
  }
}

.text {
  font-size: 14px;
  line-height: 20px;
  color: var(--color-accent);

  @include mobile {
    font-size: 12px;
    line-height: 16px;
  }
}

.btn {
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: var(--color-light);
  padding: 12px 22px;
  background: var(--color-accent);
  border: 4px solid var(--color-accent);
  border-radius: 6px;
  transition: border-color 0.3s ease-in-out;
  &--hollow {
    color: var(--color-accent);
    padding: 12px 22px;
    border: 4px solid var(--color-accent);
    background: transparent;
  }
  &--white {
    color: var(--color-accent);
    background: var(--color-light);
  }
  &:hover {
    border: 4px solid var(--color-black);
  }

  @include mobile {
    font-size: 14px;
    line-height: 18px;
  }
}

.decorate {
  @include flex-all-center;
  margin-bottom: 32px;
  &__block {
    width: 42px;
    height: 8px;
    background-color: var(--color-zest);
  }

  @include tablet {
    margin-bottom: 20px;
  }
}

.burger {
  @include burger;

  display: none;

  @include tablet {
    display: flex;
    z-index: 100;
  }
}
